import React, { useState, useEffect } from 'react'
import Papa from 'papaparse'
import BarGraph from '../components/BarGraph'
import { MdExpandMore } from 'react-icons/md'

function DistrictWide({ states }) {
    const [district, setDistrict] = useState([])
    var dis = {}
    const getStateNotes = (k) => {
        states.forEach(item => {
            if (item.State_code.localeCompare(k) === 0) return item.State_Notes
        })
    }
    const getDistrictWise = async () => {
        const data = await fetch('https://api.covid19india.org/csv/latest/district_wise.csv')
            .then(res => res.text())
        const val = Papa.parse(data, {
            header: true,
            delimiter: ',',
            newLine: '\n'
        }).data
        val.forEach(item => {
            if (dis[item["State_Code"]] === undefined) dis[item["State_Code"]] = [item]
            else dis[item["State_Code"]].push(item)
        })
        dis = Object.keys(dis).sort().reduce(
            (obj, key) => {
                obj[key] = dis[key];
                return obj;
            }, {}
        )
        /* Object.keys(dis).forEach(key => dis[key].
            sort((a, b) => {
                if (a.District < b.District) return 1
                else if (a.District > b.District) return -1
                return 0
            })) */
        setDistrict(dis)
        console.log(dis)
    }
    // "Foreign Evacuees","Unknown"
    useEffect(() => {
        getDistrictWise()
    }, [])
    return (
        <div className="container-fluid my-3">
            <div className="accordion accordion-flush" id="accordion">
                {
                    Object.keys(district)
                        .map(key => (
                            <div className="accordion-item" key={key}>
                                <div className="accordion-header" id={key}>
                                    <a className="accordion-button collapsed list-group-item state-button" data-toggle="collapse" data-target={`#collapse${key}`}>{district[key][0].State}<MdExpandMore style={{ float: "right" }} /></a>
                                </div>
                                <div className="accordion-collapse collapse" id={`collapse${key}`}>
                                    <div className="accordion-body">
                                        <h3>State Notes</h3>
                                        <p>{getStateNotes(key)}</p>
                                        <div className='table-responsive-sm my-3' id="state-wide-table">
                                            <table className="table table-sm table-striped text-center table-hover">
                                                <thead className="thead-dark">
                                                    <tr>
                                                        <th scope="col">District</th>
                                                        <th scope="col">Active</th>
                                                        <th scope="col">Confirmed</th>
                                                        <th scope="col">Recovered</th>
                                                        <th scope="col">Deceased</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {district[key].map(item => (
                                                        <tr key={item.District}>
                                                            <th>{item.District}</th>
                                                            <td>{item.Active}</td>
                                                            <td>{item.Confirmed}</td>
                                                            <td>{item.Recovered}</td>
                                                            <td>{item.Deceased}</td>
                                                        </tr>))}
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        )
                        )
                }
            </div>
        </div >
    )
}

export default DistrictWide
