import React, { useState } from 'react'
import { Link } from 'react-router-dom'

function Navbar() {
    const [hideActions, setHideActions] = useState(true)
    return (
        <div className="navbar navbar-expand-lg navbar-dark bg-primary">
            <Link className="navbar-brand" to="/">Covid-19 Tracker</Link>
            <button onClick={() => setHideActions(!hideActions)} className="navbar-toggler" type="button">
                <span className="navbar-toggler-icon"></span>
            </button>
            <div className={`${hideActions ? 'collapse' : ''} navbar-collapse ml-auto`}>
                <ul className="navbar-nav ml-auto">
                    <li className="nav-item">
                        <Link className="nav-link" onClick={() => setHideActions(!hideActions)} to="/">Home</Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" onClick={() => setHideActions(!hideActions)} to="/about">About</Link>
                    </li>
                    <li className="nav-item">
                        <Link className="nav-link" onClick={() => setHideActions(!hideActions)} to="/districts">District Wide</Link>
                    </li>
                </ul>
            </div>
        </div>

    )
}

export default Navbar
