import React from 'react'
import { Bar } from 'react-chartjs-2'

function BarGraph({ labels, label, data }) {
    return (
        <Bar className=""
            data={{
                labels: labels,
                datasets: [
                    {
                        label: label,
                        data: data,
                        borderWidth: 1,
                        color: 'white',
                    },
                ]
            }}
            options={{
                maintainAspectRatio: false,
                responsive: true,
                layout: {
                    padding: 40
                }
            }}
        />
    )
}

export default BarGraph
