import React from 'react'
import StateWide from '../components/StateWide'
import LineGraph from '../components/LineGraph'

function Home({ national, states, total }) {
    return (
        <div className="container-fluid">
            <div className="row align-items-center mt-3">
                <div className="col-lg-6">
                    <StateWide states={states} total={total} />
                </div>
                <div className="col-lg-6">
                    <LineGraph lables={national.map(item => item.Date)} label="Daily Confirmed" data={national.map(item => item["Daily Confirmed"])} color='red' classN="row graph m-2" />
                    <LineGraph lables={national.map(item => item.Date)} label="Daily Deceased" data={national.map(item => item["Daily Deceased"])} color='crimson' classN="row graph m-2" />
                    <LineGraph lables={national.map(item => item.Date)} label="Total Confirmed" data={national.map(item => item["Total Confirmed"])} color='tomato' classN="row graph m-2" />
                    <LineGraph lables={national.map(item => item.Date)} label="Total Deceased" data={national.map(item => item["Total Deceased"])} color='coral' classN="row graph m-2" />
                    <LineGraph lables={national.map(item => item.Date)} label="Total Recovered" data={national.map(item => item["Total Recovered"])} color='green' classN="row graph m-2" />
                </div>
            </div>
        </div>
    )
}
export default Home